<template>
  <div class="v-orders-item">
    <div class="one_order">
      <table class="text">
        <tr class="head">
          <td class="leftcol">  {{order_data.id}}  {{ order_data.created[2] }}.{{ order_data.created[1] }}.{{ order_data.created[0] }}</td>
          <td class="rightcol"></td>
          <td class="rightcol">: {{status}}</td>
        </tr>
        <tr class="gray">
          <td class="leftcol"> : {{order_data.delivery}}</td>
          <td class="rightcol"> : {{order_data.paymentMethod}}</td>
          <td class="rightcol">: {{order_data.allPrice}}</td>
        </tr>

        <tr class="gray">
          <td class="leftcol"><b> :</b></td>
          <td class="rightcol"></td>
          <td class="rightcol"></td>
        </tr>

        <tr class="gray">
          <td class="leftcol ml">:</td>
          <td class="rightcol"></td>
          <td class="rightcol"></td>
        </tr>
      </table>
      <vMyProductItem
          v-for="product in order_data.products"
          :key="product.id"
          v-bind:product_data="product"
      />
    </div>
  </div>
</template>

<script>
import vMyProductItem from "@/components/order/v-my-product-item";

export default {
  name: "v-orders-item",
  components: {
    vMyProductItem
  },
  props: {
    order_data: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  computed:{
    status(){
      let text = ""
      if(this.order_data.orderStatus === "_"){
        text = " "
      }
      return text
    }
  }
}
</script>

